@import "~style";

$block-name: "b-form";

.#{$block-name} {
	&__title-container {
		display: flex;
		flex-flow: wrap;
		flex-direction: column;
		align-content: center;
		justify-content: center;
		align-items: baseline;
		text-transform: uppercase;
	}

	&__title {
		display: flex;
		flex-direction: row;
		justify-content: center;
		line-height: 132%;
		font-family: $body-font-family;
		font-weight: 400;
		font-size: 2rem;
		padding: 14px 16px;
	}

	&__feedback {
		font-family: $body-font-family;
		display: flex;
		flex-direction: column;
		align-content: space-around;
		justify-content: center;
		align-items: center;
	}

	&__input-container {
		width: 35%;
		display: flex;
		flex-direction: column;
		align-items: center;
		justify-content: center;

		p {
			margin: 0;
		}

		&--error {
			input,
			textarea {
				border: 2px solid red;
			}

			.b-error {
				display: block;
			}
		}
	}
}
