// Пользовательские переменные

// Цвета, используемые на сайте (по возможности желательно придерживаться этого формата)
$color-white: #fff;
$color-black: #0a0a0a;
$color-gray: #cacaca;
$color-gray-light: #e6e6e6;
$color-gray-dark: #8a8a8a;
$color-gray-dete: #ecf2f5;

$color-purple: #9b407a;
$color-purple-dark: #2c145e;

// Основные параметры body (если не используете, удаляйте)
$body-background: $color-white;
$body-text-color: $color-black;
$body-font-family: "Open Sans", arial, sans-serif;

// Настройки сетки (если не используете, удаляйте)
// $grid-columns: 12;
// $global-width: 1200px;

// Точки разрыва адаптивной вёрстки и их настройки
$breakpoints: (
	small: 0,
	medium: 768px,
	large: 1024px,
	xlarge: 1280px,
);
$grid-gutter: (
	small: 20px,
	medium: 20px,
	large: 20px,
	xlarge: 20px,
);
